.advantages {
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: var(--background-color);
    @include tablet {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    @include desktop {
        padding-top: 40px;
        padding-bottom: 120px;
    }

    &__number {
        font-family: var(--secondary-font-family), sans-serif;
        @include font (36px, 400, 1.14);
        letter-spacing: 0.04em;
        color: var(--accent-text-color);
        margin-bottom: 20px;
        @include tablet {
            @include font (48px, 400, 1.14);
        }
        @include desktop {
            @include font (58px, 400, 1.14);
        }
    }

    &__text {
        @include font(12px, 700, 1.7);
        @include tablet {
                @include font (14px, 700, 1.74);
            }
        @include desktop {
            @include font (16px, 700, 1.76);
        }

    }

    &__list {
        @include grid-wrap;
        @include tablet {
            @include grid-wrap (flex, -20px, -20px, wrap);
        }
        @include desktop {
            @include grid-wrap (flex, -125px, -125px, wrap);
        }
    }

    &__item {
        @include grid-item;
        max-width: 230px;
        @include tablet {
            @include grid-item (20px, 20px);
            max-width: 162px;
        }
        @include desktop {
            @include grid-item (125px, 125px);
            max-width: 265px;
        }
    }
}

.advantages__item::before {
    content: " ";
    display: block;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    margin-bottom: 16px;
    width: 50px;
    height: 50px;
}

.advantages__item--adder::before {
    background-image: url(../images/advantages/cow-udder-mob-1x.png);
    @include retina {
        background-image: url(../images/advantages/cow-udder-mob-2x.png);
    }
}

.advantages__item--apples::before {
    background-image: url(../images/advantages/apples-mob-1x.png);
    @include retina {
        background-image: url(../images/advantages/apples-mob-2x.png);
    }
}

.advantages__item--heart::before {
    background-image: url(../images/advantages/heart-mob-1x.png);
    @include retina {
        background-image: url(../images/advantages/heart-mob-2x.png);
    }
}

.advantages__number {
    display: inline-block;
    animation-name: rotation;
    animation-duration: 20s;  
    animation-timing-function: cubic-bezier(.2, .88, .82, .15);
    animation-iteration-count: infinite;
@keyframes rotation {
        0% {
            transform: rotateZ(0deg) scale(1);
        }
        3% {
            transform: rotateZ(20deg) scale(0.8);
        }
        6% {
            transform: rotateZ(0deg) scale(1);
        }
        9% {
            transform: rotateZ(-20deg) scale(0.8);
        }
        12% {
            transform: rotateZ(0deg) scale(1);
        }
        15% {
            transform: rotateZ(20deg) scale(0.8);
        }
        18% {
            transform: rotateZ(0deg) scale(1);
        }
        21% {
            transform: rotateZ(-20deg) scale(0.8);
        }
        24% {
            transform: rotateZ(0deg) scale(1);
        }
        88% {
            transform: rotateZ(0deg) scale(1);
        }
        91% {
            transform: rotateZ(20deg) scale(0.8);
        }
        94% {
            transform: rotateZ(0deg) scale(1);
        }
        97% {
            transform: rotateZ(-20deg) scale(0.8);
        }
        100% {
            transform: rotateZ(0deg) scale(1);
        }
    }
}


